const defaultState = {
  currentUser: {
    id: 0,
    username: "wjakah01",
    email: "wade.jakahi@chsli.org",
    fullname: "Current User",
    role: "user",
    role1: "administrator",
    jwt: ""
  }
};
const getters = {
  CURRENT_USER: state => {
    return state.currentUser;
  }
};
const mutations = {
  SET_CURRENT_USER: (state, payload) => {
    state.currentUser = payload;
  }
};
const actions = {
  SET_CURRENT_USER: ({ commit }, payload) => {
    commit("SET_CURRENT_USER", payload);
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
