import Axios from "axios";
import Config from "../../config";

const defaultState = {
  audits: []
};
const getters = {
  AUDITS: state => {
    return state.audits;
  }
};
const mutations = {
  SET_AUDITS: (state, payload) => {
    state.audits = payload;
  },
  UPDATE_AUDITS: (state, payload) => {
    state.audits = payload;
  }
};
const actions = {
  FETCH_AUDITS: async ({ context }, payload) => {
    let { data } = await Axios.get(Config.apiRoot + "/audits/" + payload.auditor);
    context.commit("SET_AUDITS", data);
  },
  UPDATE_AUDITS: async ({ commit }, payload) => {
    // only set the fields that are "editable"
    const filteredPayload = {
      audit_id: payload.audit_id,
      unitsAudited: payload.unitsAudited
    };
    // const res = await Axios.post("http://localhost:3000/physicians", filteredPayload);
    // if (!res.success) {
    //   return false;
    // }
    commit("UPDATE_AUDITS", filteredPayload);
    return true;
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};

