import Axios from "axios";
import Config from "../../config";

const defaultState = {
  practices: []
};
const getters = {
  PRACTICE: state => {
    return state.practices;
  }
};
const mutations = {
  SET_PRACTICE: (state, payload) => {
    state.practices = payload;
  },
  UPDATE_PRACTICE: (state, payload) => {
    const id = state.practices.findIndex(elem => elem.id === payload.id);

    state.practices[id] = Object.assign({}, payload);
  },
  NEW_PRACTICE: (state, payload) => {
    state.practices.push(payload);
  }
};
const actions = {
  FETCH_PRACTICE: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/practice");
    context.commit("SET_PRACTICE", data);
  },
  UPDATE_PRACTICE: async ({ commit }, payload) => {
    const res = await Axios.put(Config.apiRoot + "/practice/" + payload.id, payload);
    if (!res.data.success) {
      return res.data.message;
    }
    commit("UPDATE_PRACTICE", payload);
    return true;
  },
  NEW_PRACTICE: async ({ commit }, payload) => {
    const res = await Axios.post(Config.apiRoot + "/practice", payload);
    if (!res.data.success) {
      return res.data.message;
    }
    payload.id = res.data.new_id;
    commit("NEW_PRACTICE", payload);
    return true;
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
